	module.exports = {"data":{"markdownRemark":{"html":"<h2>U Tag</h2>\n<p>The HTML <code>&#x3C;u></code> tag renders text with an underline.</p>\n<p>The <code>&#x3C;u></code> element was deprecated in HTML 4.01. In HTML5, the <code>&#x3C;u></code> tag was redefined to represent text that should be stylistically different form normal text. This includes misspelled words or proper nouns in Chinese.</p>\n<p>Instead, to underline text, it is recommended that the <code>&#x3C;span></code> tag is used in place of <code>&#x3C;u></code>. Style your <code>&#x3C;span></code> tags with the CSS <code>text-decoration</code> property with the value <code>underline</code>.</p>\n<h3>Examples:</h3>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>html</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>body</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>p</span><span class=\"token punctuation\">></span></span>This parragraph has a <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>u</span><span class=\"token punctuation\">></span></span>underline<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>u</span><span class=\"token punctuation\">></span></span>.<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>p</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>body</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>html</span><span class=\"token punctuation\">></span></span>\n</code></pre>\n      </div>\n<p>Underlining text with the <code>&#x3C;span></code> tag:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>span</span><span class=\"token style-attr language-css\"><span class=\"token attr-name\"> <span class=\"token attr-name\">style</span></span><span class=\"token punctuation\">=\"</span><span class=\"token attr-value\"><span class=\"token property\">text-decoration</span><span class=\"token punctuation\">:</span> underline</span><span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>Everyone<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>span</span><span class=\"token punctuation\">></span></span> has been talking about <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>span</span><span class=\"token style-attr language-css\"><span class=\"token attr-name\"> <span class=\"token attr-name\">style</span></span><span class=\"token punctuation\">=\"</span><span class=\"token attr-value\"><span class=\"token property\">text-decoration</span><span class=\"token punctuation\">:</span> underline</span><span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>freeCodeCamp<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>span</span><span class=\"token punctuation\">></span></span> lately.\n</code></pre>\n      </div>\n<h3>More Information:</h3>\n<p><a href=\"https://developer.mozilla.org/en-US/docs/Web/HTML/Element/u\">MDN</a></p>","fields":{"slug":"/html/elements/u-tag/"},"frontmatter":{"title":"U Tag"}}},"pathContext":{"slug":"/html/elements/u-tag/"}}

